@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
@import url(https://fonts.googleapis.com/css?family=Quicksand&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
}
.box {
  width: 300px;
  border: 15px solid green;
  padding: 50px;
  margin: 20px;
}
.container {
  max-width: 500px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  border: 1px solid steelblue;
  padding: 30px;
  border-radius: 5px;
  -webkit-align-self: center;
          align-self: center;
}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 20px;
}

.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:focus {
  outline: none;
}

.btn:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}

.task {
  background: #f4f4f4;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
}

.task.reminder {
  border-left: 5px solid green;
}

.task h3 {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.add-form {
  margin-bottom: 40px;
}

.form-control {
  margin: 20px 0;
}

.form-control label {
  display: block;
}

.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}

.form-control-check {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.form-control-check label {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.form-control-check input {
  -webkit-flex: 2 1;
          flex: 2 1;
  height: 20px;
}

footer {
  margin-top: 30px;
  text-align: center;
}

.App {
  text-align: center;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  /* line-height: 1.15; */
  letter-spacing: -0.003em;
  font-family: "Muli", sans-serif;
}

.btnn {
  background-size: 100%;
  height: 100px;
  width: 100px;
  border-radius: 12px;
  color: rgb(3, 3, 2);
  /* padding: 15px 32px; */
  text-align: center;
  text-decoration: none;
  /* display: inline-block; */
  font-size: 32px;
}
.btn {
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 32px;
}
.show {
  margin-inline: 300px;
  font-size: 20px;
  text-align: center;
  background-color: hsla(60, 7%, 5%, 0.576);
  padding: 20px 10px;
}
.first_heading {
  background-color: #00000093;
  color: white;
  padding: 20px 10px;
}
.second_heading {
  background-color: #a71a1a93;
  color: rgb(0, 0, 0);
  padding: 20px 10px;
}
.expand {
  color: #112ca7;
  display: inline;
  margin: 10px;
}
.hover-nav li {
  float: left;
}

.items li a {
  position: relative;
  display: block;
  color: #181414;
  text-align: center;
  padding: 8px 12px;
  text-decoration: none;
  z-index: 0;
}

#row:hover * {
  background-color: rgb(15, 4, 4);
  color: white;
}

.dropdown {
  position: relative;
  font-size: 20px;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  padding: 12px 16px;
  z-index: 1;
}

.dropdown:hover .dropdown-content {
  display: block;
}
.column {
  float: left;
  width: 33.33%;
  padding: 5px;
}

.row {
  display: inline-block;
  width: 100%;
}
.row::after {
  content: "";
  clear: both;
  display: inline-block;
}

.image_button {
  -webkit-align-self: center;
          align-self: center;
  padding: 5px;
  /* padding: 20px; */
  border: 5px solid rgb(0, 0, 0);
}
.inline {
  display: inline;
}
#left {
  position: relative;
  float: left;
  padding-left: 20px;
}
#right {
  position: relative;
  float: right;
  padding-right: 20px;
}
.company_images {
  background-color: rgb(141, 132, 132);
}

